<script>
  import { onMount } from 'svelte'

  function closeModal() {
    let modal = document.querySelector('.modal')
    if (modal.classList.contains('active')) {
      modal.classList.remove('active')
    }
  }

  onMount(() => {
    document.addEventListener('open-modal', (e) => {
      let modal = document.querySelector('.modal')

      if (!modal.classList.contains('active')) {
        document.querySelector('.modal-title').innerHTML = e.detail.title
        modal.classList.add('active')
      }
    })
  })
</script>

<div class="modal" id="modal-id">
  <a href="#close" class="modal-overlay" aria-label="Close" on:click|preventDefault={ closeModal }></a>
  <div class="modal-container">
    <div class="modal-header">
      <a href="#close" class="btn btn-clear float-right" aria-label="Close" on:click|preventDefault={ closeModal }></a>
      <div class="modal-title h5"></div>
    </div>
    <div class="modal-body">
      <div class="content">

        <div class="timeline">
          <div class="timeline-item" id="timeline-example-1">
            <div class="timeline-left">
              <a class="timeline-icon" href="#timeline-example-1"></a>
            </div>
            <div class="timeline-content">
              <!-- tiles structure -->
              <p class="subtitle-title">Senin, 7 Juli 2020</p>
              <p class="title-title">Pembayaran retribusi menara jaringan</p>
            </div>
          </div>
        
          <div class="timeline-item" id="timeline-example-2">
            <div class="timeline-left">
              <a class="timeline-icon icon-lg" href="#timeline-example-2">
                <i class="icon icon-check"></i>
              </a>
            </div>
            <div class="timeline-content">
              <!-- tiles structure -->
              <p class="subtitle-title">Senin, 6 Juli 2020</p>
              <p class="title-title">Pembayaran Pajak IMB</p>
            </div>
          </div>

          <div class="timeline-item" id="timeline-example-2">
            <div class="timeline-left">
              <a class="timeline-icon icon-lg" href="#timeline-example-2">
                <i class="icon icon-check"></i>
              </a>
            </div>
            <div class="timeline-content">
              <!-- tiles structure -->
              <p class="subtitle-title">Senin, 6 Juli 2020</p>
              <p class="title-title">Pembayaran Retribusi Kendaraan</p>
            </div>
          </div>

        </div>

      </div>
    </div>
    <div class="modal-footer">
      <button class="btn" on:click|preventDefault={ closeModal }>Close</button>
    </div>
  </div>
</div>

<style>
  .modal {
    color: #24242b;
  }
</style>